import styled from "styled-components";

const ToogleSwitch = () => {
  return (
    <Wrapper className="switch">
      <CheckBox type="checkbox" id="checkbox" />
      <Slider htmlFor="checkbox" />
    </Wrapper>
  );
};

const Wrapper = styled.div`
  position: relative;
`;
const Slider = styled.label`
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 34px;
  border-radius: 15px;
  background: #bebebe;
  cursor: pointer;
  &::after {
    content: "";
    display: block;
    border-radius: 50%;
    width: 26px;
    height: 26px;
    margin: 3px;
    background: #ffffff;
    box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.2);
    transition: 0.4s;
  }
`;
const CheckBox = styled.input`
  opacity: 0;
  z-index: 1;
  border-radius: 15px;
  width: 42px;
  height: 26px;

  &:checked + ${Slider} {
    background: #01ccce;
    &::after {
      content: "";
      display: block;
      border-radius: 50%;
      width: 26px;
      height: 26px;
      margin-left: 30px;
      transition: 0.4s;
    }
    &::before {
      transform: translateX(26px);
    }
  }
`;

export default ToogleSwitch;
